/* The border for all the radio station parts. */
.rm-brd {
  border: 1px solid #3A3B3C;
  border-top: none; }
  .rm-brd:first-child {
    border: 1px solid #3A3B3C; }

/* The background for all the radio station main parts. */
.rm-bg-primary {
  background-color: #0090FF; }

/* The background for the musician description. */
.rm-bg-secondary {
  background-color: #F8F8F8; }

.rm-title-level1, .rm-title-level2, .rm-title-level3 {
  color: #FFF;
  cursor: default;
  font-weight: bold;
  font-size: 25px; }

.rm-title-level2, .rm-title-level3 {
  font-size: 20px; }

.rm-title-level3 {
  font-weight: normal;
  font-size: 15px; }

.rm-flex {
  display: flex;
  justify-content: space-between; }

.rm-icons {
  display: block;
  width: 60px; }

#rm-musician-image-field {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #3A3B3C; }
  #rm-musician-image-field img {
    display: block;
    max-width: 100%; }

#rm-musician-image {
  cursor: pointer; }

#rm-middle-field {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 10px;
  text-align: center; }

#rm-left-field,
#rm-right-field {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; }
  #rm-left-field:hover,
  #rm-right-field:hover {
    background: #C01302; }

#rm-musician-description-field {
  position: relative;
  height: 6em;
  overflow: hidden; }

#rm-musician-description-gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, #f8f8f8 15%, rgba(255, 0, 0, 0) 100%); }

#rm-musician-description {
  cursor: default; }
